import React, { Component } from 'react';
import { Container, Table, Tabs, Tab } from 'react-bootstrap';

const paper_title1 = 'Trust-aware recommender systems';
const paper_link1 = 'https://medium.com/@arthurlee_73761/review-for-trust-aware-recommender-systems-992fa997b02';

const paper_title2 = 'Performance of recommender algorithms on top-n recommendation tasks';
const paper_link2 = 'https://medium.com/@arthurlee_73761/review-for-performance-of-recommender-algorithms-on-top-n-recommendation-tasks-9b3af197072';

const paper_title3 = 'Multiverse Recommendation: N-dimensional Tensor Factorization for Context-aware Collaborative Filtering';
const paper_link3 = 'https://medium.com/@arthurlee_73761/review-for-multiverse-recommendation-n-dimensional-tensor-factorization-for-context-aware-d024da1a279c';

const paper_title4 = 'A Matrix Factorization Technique with Trust Propagation for Recommendation in Social Networks';
const paper_link4 = 'https://medium.com/@arthurlee_73761/a-matrix-factorization-technique-with-trust-propagation-for-recommendation-in-social-networks-b14b0ea2d848';


function PaperCard(props) {
  return (
    <p className="paper-text">{props.title} - &nbsp;
      <a target="_blank" href={props.link}><span className="fa fa-external-link" /></a>
    </p>
  )
}

function RecommendationSystem() {
  return (
    <div className='div-left'>
      <Table striped bordered hover size="sm">
        <tbody>
          <tr>
            <td><PaperCard
              title={paper_title1}
              link={paper_link1}
            /></td>
          </tr>
          <tr>
            <td><PaperCard
              title={paper_title2}
              link={paper_link2}
            /></td>
          </tr>
          <tr>
            <td><PaperCard
              title={paper_title3}
              link={paper_link3}
            /></td>
          </tr>
          <tr>
            <td><PaperCard
              title={paper_title4}
              link={paper_link4}
            /></td>
          </tr>
        </tbody>
      </Table>
    </div>
  )
}
function DeepLearning() {
  return (
    <div className='div-left'>
      <h2>Reading...&nbsp;
      </h2>
    </div>
  )
}



function PaperChallenge() {
  return (
    <Container>
      <div className='u-center-text u-margin-bottom-big'>
        <h1>Paper Challenge</h1>
        <h2>Paper Challenge for different fields.</h2>
      </div>
      <Tabs
        defaultActiveKey="recommendation_system" transition={false} id="noanim-tab-example"
      >
        <Tab eventKey="recommendation_system" title="RecommendationSystem">
          <div className='u-center-text u-margin-bottom-big'>
            <RecommendationSystem />
          </div>
        </Tab>
        <Tab eventKey="deep_learning" title="DeepLearning">
          <div className='u-center-text u-margin-bottom-big'>
            <DeepLearning />
          </div>
        </Tab>
      </Tabs>
    </Container>
  )
}

function MainContent({ is_home }) {
  if (is_home) {
    return (
      <div id="course" className="paper-style">
        <PaperChallenge />
      </div>
    );
  } else {
    return (
      <div id="course" className="paper-style">
        <PaperChallenge />
      </div>
    );
  }
}

class Book extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <MainContent is_home={this.props.is_home} />
    )
  }

}
export default Book;